<template>
    <header class="bg-white border-b fixed  w-full ">
        <nav class="grid grid-cols-3 px-3 py-2  justify-between">
            <div class="relative item text-blue-300" @click="changeBell">
                <transition name="slide-fade" mode="out-in">
                    <div v-if="bell">
                        <router-link to="/home">
                            <img src="../assets/logo.png" class="w-6 filter" alt="" />
                        </router-link>
                    </div>
                    <div v-else>
                        <router-link to="/notifications">
                            <i class="fa fa-bell-o" aria-hidden="true"></i>
                        </router-link>
                    </div>
                </transition>
            </div>

            <!-- center -->
            <div class="item mx-auto text-blue-500 relative">
                <transition name="fade" mode="out-in">
                    <div v-if="cog || bell">
                        <div v-if="cog" class=" absolute active pt-1" id="centerCog">
                            <i class="fa fa-cog" aria-hidden="true"></i>
                        </div>
                        <div v-else class=" absolute active pt-1" id="centerBell">
                            <i class="fa fa-bell-o" aria-hidden="true"></i>
                        </div>
                    </div>
                    <div v-else>
                        <img
                            src="../assets/logo.png"
                            class="w-6 absolute active"
                            id="centerImg"
                            alt=""
                        />
                    </div>
                </transition>
            </div>

            <div class="item ml-auto text-blue-300" @click="changeCog">
                <transition name="slide-fade" mode="out-in">
                    <div v-if="cog">
                        <router-link to="/home">
                            <img src="../assets/logo.png" class="w-6 filter" alt="" />
                        </router-link>
                    </div>
                    <div v-else>
                        <router-link to="/settings">
                            <i class="fa fa-cog" aria-hidden="true"></i>
                        </router-link>
                    </div>
                </transition>
            </div>
        </nav>
    </header>
</template>

<script>
import { ref } from "vue"
export default {
    setup() {
        const active = ref(true)
        const user = ref(true)
        const bell = ref(false)
        const cog = ref(false)

        const changeBell = () => {
            bell.value = !bell.value
            if (cog.value) {
                cog.value = !cog.value
            }
            user.value = !user.value
        }
        const changeCog = () => {
            cog.value = !cog.value
            if (bell.value) {
                bell.value = !bell.value
            }
            user.value = !user.value
        }
        return {
            user,
            bell,
            cog,
            active,
            changeBell,
            changeCog,
        }
    },
}
</script>

